import * as Yup from 'yup';

const currentYear = new Date().getFullYear();

export const validationDoctorPageSchema = Yup.object().shape({
  firstName: Yup.string()
    .matches(
      // eslint-disable-next-line no-useless-escape
      `^(?!.*[])[-- -')]+$`,
      '  .    .'
    )
    .required(" '")
    .max(64, '    64 '),
  lastName: Yup.string()
    .matches(
      // eslint-disable-next-line no-useless-escape
      `^(?!.*[])[-- -')]+$`,
      '  .    .'
    )
    .required(' `')
    .max(64, '    64 '),
  patronymic: Yup.string()
    .matches(
      // eslint-disable-next-line no-useless-escape
      `^(?!.*[])[-- -')]+$`,
      '  .  -  .'
    )
    .max(64, '-    64 '),
  phones: Yup.array().of(
    Yup.string()
      .required(' `')
      .test(
        'is-unique',
        '    ',
        function (value) {
          //  Set      
          const uniqueValues = [...new Set(value)];
          return value.length !== 0
            ? true
            : uniqueValues.length === value.length;
        }
      )
      .matches(
        // eslint-disable-next-line no-useless-escape
        '^\\+\\d{12}$',
        '   . ,       .'
      )
  ),
  dateOfBirthday: Yup.date()
    .min(
      new Date('1900-01-01'),
      ' ,    .'
    )
    .max(new Date(), ' ,    .'),
  instagram: Yup.string().matches(
    /^(https?:\/\/)?(www\.)?instagram\.com\/[a-zA-Z0-9_.]+\/?$/,
    '  URL- Instagram-'
  ),
  tiktok: Yup.string().matches(
    /^(https?:\/\/)?(www\.)?tiktok\.com\/@[a-zA-Z0-9_.]+\/?$/,
    '  URL- TikTok-'
  ),
  otherLink: Yup.string().url('  URL-'),
  paymentMethods: Yup.array().of(
    Yup.string().oneOf(
      ['cash', 'card'],
      '   " "  " "'
    )
  ),
  experienceYears: Yup.number()
    .required(' `')
    .min(0, '       0 ')
    .max(100, '       100 '),
  educations: Yup.array().of(
    Yup.object().shape({
      id: Yup.string(), // ID - ,  '
      name: Yup.string().max(
        120,
        '     120 .'
      ), //   - ,  '
      years: Yup.object().shape({
        begin: Yup.number()
          .required("   ' ")
          .min(1990, '      1990')
          .max(
            currentYear,
            '       '
          ),
        end: Yup.number()
          .nullable()
          .when('begin', (begin, schema) => {
            if (begin) {
              return schema.min(
                begin,
                '        '
              );
            }
            return schema;
          }),
      }),
    })
  ),
});
