import Container from 'componentsReusable/Container/Container';
import SideBarPage from 'componentsReusable/SideBarPage/SideBarPage';
import { StyledNavLink } from './MyPage.styled';
import { useAuth } from 'hooks';
import { Outlet } from 'react-router-dom';
import {
  MainContainer,
  StyledPageContainer,
} from '../UserPage/UserPage.styled';
import HeaderPage from 'componentsReusable/HeaderPage/HeaderPage';

const MyPage = () => {
  const { userType } = useAuth();

  return (
    <Container>
      <StyledPageContainer>
        <HeaderPage
          title={`  ${
            userType === 'doctor' ? '' : ''
          }`}
        />
        <MainContainer style={{ display: 'flex' }}>
          <SideBarPage>
            <StyledNavLink to="accountData"> </StyledNavLink>
            <StyledNavLink to="personalData"> </StyledNavLink>
            {userType === 'doctor' && (
              <StyledNavLink to="directionWork"> </StyledNavLink>
            )}
            {userType === 'doctor' && (
              <StyledNavLink to="communication"></StyledNavLink>
            )}
            <StyledNavLink to="chat"></StyledNavLink>
          </SideBarPage>
          <Outlet />
        </MainContainer>
      </StyledPageContainer>
    </Container>
  );
};

export default MyPage;
