import * as Yup from 'yup';

export const validationRefreshPasswordSchema = Yup.object().shape({
  password: Yup.string().required(' `'),
  newPassword: Yup.string()
    .required(' `')
    .min(8, '     8 ')
    .max(32, '    32 ')
    .matches(
      /^(?=.*[A-Z])(?=.*\d).{8,16}$/,
      '    8  ,   ,  '
    ),
  confirmNewPassword: Yup.string()
    .oneOf([Yup.ref('newPassword'), null], '  ')
    .required(' `'),
});
