import { useAuth } from 'hooks';
import {
  BurgerMenuContainer,
  StyledBtnLogOut,
  StyledNavLink,
} from './BurgerMenu.styled';

const BurgerMenu = ({ setIsOpenModalSignOut }) => {
  const { isLoggedIn } = useAuth();
  return (
    <>
      <BurgerMenuContainer>
        {isLoggedIn ? (
          <>
            <StyledNavLink to="user/accountData"> </StyledNavLink>
            <StyledBtnLogOut onClick={() => setIsOpenModalSignOut(true)}>
              
            </StyledBtnLogOut>
          </>
        ) : (
          <>
            <>
              <StyledNavLink to="/login"></StyledNavLink>
              <StyledNavLink to="/register"></StyledNavLink>
            </>
          </>
        )}
      </BurgerMenuContainer>
    </>
  );
};

export default BurgerMenu;
