import * as Yup from 'yup';

export const validationRegisterSchema = Yup.object().shape({
  email: Yup.string()
    .required(' `')
    .email('    '),
  password: Yup.string()
    .required(' `')
    .min(8, '     8 ')
    .max(32, '    32 ')
    .matches(
      /^(?=.*[A-Z])(?=.*\d).{8,16}$/,
      '    8  ,   ,  '
    ),
  confirmPassword: Yup.string()
    .oneOf([Yup.ref('password'), null], '  ')
    .required(' `'),
});
